import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Observable} from "rxjs";

const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'my-auth-token'
    })
};

@Injectable({
    providedIn: 'root'
})
export class PresupuestosService {

    databaseUrl = 'https://proyecto-angular-75fbc.firebaseio.com/presupuestos.json';
    preDatabaseUrl = 'https://proyecto-angular-75fbc.firebaseio.com/presupuestos';

    constructor(private http: HttpClient) {
    }

    postPresupuesto(presupuesto: any): Observable<any> {
        const newpres = JSON.stringify(presupuesto);

        return this.http.post(this.databaseUrl, newpres, httpOptions);
    }

    getPresupuestos() {
        return this.http.get(this.databaseUrl);
    }

    getPresupuesto(id$: string) {
        const url = `${ this.preDatabaseUrl }/${ id$ }.json`;
        return this.http.get(url);
    }

    putPresupuesto(presupuesto: any, id$: string) {
        const newpre = JSON.stringify(presupuesto);
        const url = `${ this.preDatabaseUrl }/${ id$ }.json`;

        return this.http.put(url, newpre, httpOptions);
    }

    delPresupuesto ( id$: string ) {
        const url = `${ this.preDatabaseUrl }/${ id$ }.json`;
        return this.http.delete(url);
    }
}
